*,
*:before,
*:after {
  box-sizing: border-box;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  margin: 0;
  padding: 0;
  border: 0;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, "avenir next", avenir,
    "helvetica neue", helvetica, ubuntu, roboto, noto, "segoe ui", arial,
    sans-serif;
  font-weight: 400;
  line-height: 1.5;
  font-size: 1rem;
  hyphens: auto;
  color: #001319;
}

@media (min-width: 48em) {
  body {
    font-size: 1.125rem;
  }
}

code,
var,
kbd,
samp,
tt,
dir,
listing,
plaintext,
xmp,
abbr,
acronym,
blockquote,
input,
textarea,
q {
  hyphens: none;
}

a {
  text-decoration: none;
}

button {
  padding: 0;
  margin: 0;
  border: none;
  font: inherit;
  color: inherit;
  background-color: transparent;
  cursor: pointer;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

ol,
ul {
  list-style: none;
}

img,
video {
  max-width: 100%;
}

img {
  border-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote:after,
blockquote:before,
q:after,
q:before {
  content: "";
  content: none;
}

.resultContainer {
  width: 45%;
  padding: 1rem;
  position: absolute;
  top: 10%;
  left: 50%;
  transform: translate(-50%);
}

.resultList {
  margin: 10px 10px;
  padding: 15px;
}
ul {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(120px, 1fr));
}

ul li {
  display: inline;
  margin: 10px 9px;
  padding: 10px;
  background-color: #f8f8f8;
  border-radius: 5px;
  text-align: center;
}

ul li:hover {
  cursor: pointer;
  background-color: #1fa7fa;
}

.selected {
  background-color: #1fa7fa;
  color: white;
}
